import {displayNoneToggle} from '../Tools/codeTools'
import ytAudioHandle from './ytAudioStop'

var curNavState = { } //object defining state of navigation (eg what tool is selected and what are those properties etc)
function initCurNavState(){
        curNavState = { 
            CurrentToolString: "Info/About", //string of tool (ParentNav/ChildNav)
            CurrentToolPage: document.querySelector(".info-about-wrap"), //qs of tool page wrap (eg vision-RGB-wrap)
            currentToolNav: [document.querySelector(".main-nav-link.Info"),"init"], //init is initial lp//document.queryselector(".drp-dwn-child.selected")
            Browsing: new Array,//becomes qs of browsing dad nav (eg "vision", visions element)
    }
}

var prvSlctdNav = ''; //refers to previously selected navigation parent (eg Vision, which has 3 children navs)

//update Browsing state (unless "click" is audio, then nav to audio page)
function updateBrowsingState(e){
    //update curNavState
    setTimeout(() => {
        //debugger;

        switch(e.target.parentElement.classList.contains("Audio")){ 

            case(true): //MASTER AUDIO SWITCH CODE

                //check if audio has lazy loaded yet
                var hasLoaded = () => {
                    if(document.querySelector(".audio-tool-wrap")){ 
                        return true
                    } else { return false }
                }

            if(hasLoaded()){
                ytAudioHandle(e.target.dataset.toolType,e.target)
                //debugger;
                //ytAudioHandle(e.target.dataset.toolType,e.target)
                    //NON AUDIO-NAV SELECTED -> AUDIO NAV SELECTED
                    if (curNavState.Browsing.length > 1 && prvSlctdNav != e.target){
                        
                        prvSlctdNav = e.target;
                        curNavState.Browsing = new Array;
                        [e.target.innerText, e.target.parentElement].forEach((item) => {
                            curNavState.Browsing.push(item);
                        })
                    }
                    //NO NAV SELECTED -> AUDIO NAV SELECTED
                    else if(curNavState.Browsing.length == 0){ 
                        [e.target.innerText, e.target.parentElement].forEach((item) => {
                            prvSlctdNav = e.target;
                            curNavState.Browsing.push(item);
                        })   
                    }
                    //AUDIO SWITCH FUNCTION
                        //update tool string 
                        curNavState.CurrentToolString = e.target.innerText 
                        //update tool nav
                        curNavState.currentToolNav = new Array;
                        curNavState.currentToolNav.push(curNavState.Browsing[1]);
                        curNavState.Browsing = new Array;
                        //update tool page
                        //displayNone curNavState.CurrentToolPage (so last tool) & remove element w/ classList of selected-tool-page of said class
                        displayNoneToggle(curNavState.CurrentToolPage);
                        curNavState.CurrentToolPage.classList.remove("selected-tool-page");
                        //qs the data thats in e.target, use that to select the tool-page-wrap u need to remove display-none & modify classlist & state obj
                        displayNoneToggle(document.querySelector(".audio-tool-wrap"));
                        document.querySelector(".audio-tool-wrap").classList.add("selected-tool-page")
                        curNavState.CurrentToolPage = document.querySelector(".selected-tool-page");
                    }
                    else if(hasLoaded() == false){
                        setTimeout(() => {
                            updateBrowsingState(e);
                            console.log("check for if audio has loaded again")
                        }, 100);
                    }


                break;
            case(false): //UPDATE BROWSING STATE FOR NON AUDIO NAVS
                //NAV SELECTED -> NEW NAV SELECTED
                if (curNavState.Browsing.length > 1 && prvSlctdNav != e.target){
                    prvSlctdNav = e.target;
                    curNavState.Browsing = new Array;
                    [e.target.innerText, e.target.parentElement].forEach((item) => {
                        curNavState.Browsing.push(item);
                    })
                }
                //NAV SELECTED -> NO NAV SELECTED
                else if(curNavState.Browsing.length > 1 && prvSlctdNav == e.target){
                    curNavState.Browsing = new Array;
                }
                //NO NAV SELECTED -> NAV SELECTED
                else if(curNavState.Browsing.length == 0){ 
                    [e.target.innerText, e.target.parentElement].forEach((item) => {
                        prvSlctdNav = e.target;
                        curNavState.Browsing.push(item);
                    })
                }
                break;
        }

    }, 100);
}

//UPDATE TOOL PAGE, TOOL STRING & TOOLNAV (when user clicks child nav)
function updateCurrentState(e){
debugger;

    //determines whether or not vision or AV has lazy loaded yet, if it has it returns true vice verse - if false simply run function again in 100ms
    var navType = () => {
        if(e.target.dataset.toolType == "Greyscale" || e.target.dataset.toolType == "RGB Pattern" || e.target.dataset.toolType ==  "Solid Color" || e.target.dataset.toolType ==  "Stopwatch"){
            if(document.querySelector(".vision-greyscale-wrap")){
                return true
            } else { return false }
        }
        else if(e.target.dataset.toolType == "AV Sync Test" || e.target.dataset.toolType ==  "Frequency Sweep" || e.target.dataset.toolType ==  "Music Video" || e.target.dataset.toolType == "Left Right Test"){
            if(document.querySelector(".AV-sync-wrap")){
                return true
            } else { return false }
        }
    }
//debugger;
    if (e.target.innerText != "Audio" && e.target.classList.contains("drp-dwn-child") && navType() 
    || e.target.dataset.toolType == "Instructions" || e.target.dataset.toolType == "About"){ //if target not audio tool

        //populate curNavState.toolString (get previous browsing string and e.target.innerText)
        curNavState.CurrentToolString = `${curNavState.Browsing[0]}/${e.target.innerText}`;   

        //populate curNavState.toolNav with parent and child nav (clearing previous .browsing and .toolNav data)
        curNavState.currentToolNav = new Array;
        [curNavState.Browsing[1],e.target].forEach(item => curNavState.currentToolNav.push(item));
        curNavState.Browsing = new Array;

        //switching tool views (eg vision rgb -> av sync)
            
            //displayNone curNavState.CurrentToolPage (so last tool) & remove element w/ classList of selected-tool-page of said class
            displayNoneToggle(curNavState.CurrentToolPage);
            curNavState.CurrentToolPage.classList.remove("selected-tool-page");
            
            ytAudioHandle(e.target.dataset.toolType,e.target)//handles turning off/on youtube audio when needed

            //show the selected tool (remove .display-none etc)
            function apndVsnImg(visionTool){
                displayNoneToggle(visionTool);
                visionTool.classList.add("selected-tool-page")
                curNavState.CurrentToolPage = document.querySelector(".selected-tool-page");
            }

            //qs the data thats in e.target, use that to select the tool-page-wrap u need to remove display-none & modify classlist & state obj
            function checkImg(data,query){ debugger;
                console.log(`the dataset is ${data} and the query is ${query}`)
                if(e.target.dataset.toolType == data){
                    //if element has been injected into the browser yet, append tool (eg greyscale)
                    if(query){
                        apndVsnImg(query);
                    //if element has not yet loaded into browser, check again in 50ms
                    } else if (!query){
                        setTimeout(() => {
                            //checkImg(data,query);
                            runCheckImg();
                        }, 50);
                    }
                }
            }
            
            function runCheckImg(){
            //Vision Tools
                checkImg("Greyscale",document.querySelector(".vision-greyscale-wrap")); // greyscale
                checkImg("RGB Pattern",document.querySelector(".vision-RGB-wrap")); // RGB
                checkImg("Solid Color",document.querySelector(".vision-solid-wrap")); // Solid Color
                checkImg("Stopwatch",document.querySelector(".vision-stopwatch-wrap")); // Stopwatch

            //AV Tools
                checkImg("AV Sync Test",document.querySelector(".AV-sync-wrap")); // AV Sync
                checkImg("Frequency Sweep",document.querySelector(".AV-freqsweep-wrap")); // Frequency Sweep
                checkImg("Music Video",document.querySelector(".AV-musicvid-wrap")); // Music Video
                checkImg("Left Right Test",document.querySelector(".AV-LR-wrap")); // Leftright test

            //Info options
                checkImg("Instructions",document.querySelector(".info-instructions-wrap")); // Instructions
                checkImg("About",document.querySelector(".info-about-wrap")); // About
                checkImg("Contact",document.querySelector(".info-contact-wrap")); // Contact
                checkImg("Privacy Policy",document.querySelector(".info-privacy-wrap")); // Privacy Policy
            } runCheckImg();
    } 
    else if(navType() == false){
        //debugger
        setTimeout(() => {
            updateCurrentState(e)
        }, 100);
    }
}

//master state set 
export {updateBrowsingState, updateCurrentState, initCurNavState}


