//JAVASCRIPT FOR ADAPTIVE NAV ANIMATION      < -> X

    function navIconShapeShift(iconOne,iconTwo){
        debugger;
    //if in adptv mode so < 1000px wide
    //if tab is closed, remove .closed and add .open
    //else if tab is open, remove .open and add .closed

        if(iconOne.classList.contains("closed")){
            [iconOne,iconTwo].forEach( item => {
                item.classList.remove("closed");
                item.classList.add("open");
                });
        }
        else if(iconOne.classList.contains("open")){
            [iconOne,iconTwo].forEach( item => { 
                item.classList.remove("open");
                item.classList.add("closed")
            });

        }
    }

export { navIconShapeShift }