import React, { lazy, Suspense } from 'react'
import { initSgConfing } from './Audio/Audio_Functions/Audio_Sg_Init';
import { InfoWorkSpace } from './Info/Info_WS';
import { apndJq,apndJqUI } from '../Tools/codeTools.js'

const AudioWorkSpace = lazy(() => import('./Audio/audio_WS'));
const AVWorkSpace = lazy(() => import('./AV/AV_WS'));
const VisionWorkSpace = lazy(() => import('./Vision/vision_WS'));

var initMount = false

class ParentWorkSpace extends React.Component {
    constructor(props){
        super(props);
        this.state = { 
            Audio: false,
            Vision: false,
            AV: false,
            InfoWorkSpace: true,
        }
    }

    renderLoader = () => <p style={{display: "none"}}>loading</p>;

    changeState(tool){
        //debugger;
        if(tool.innerHTML === "Audio" || tool.parentElement.classList.contains("drp-dwn-nav")){
            if(tool.innerHTML === "Audio") { this.setState({Audio: true}); }
            else if (tool.innerText == "Greyscale" || tool.innerText == "RGB Pattern" || tool.innerText == "Solid Color" || tool.innerText == "Stopwatch") { this.setState({Vision: true}) }
            else if (tool.innerText == "AV Sync Test" || tool.innerText == "Frequency Sweep" || tool.innerText == "Music Video" || tool.innerText == "Left Right Test") { 
                this.setState({AV: true}) }
        }
    }
    componentDidMount(){
        //only run below code on initial mount, not when component is rerun for lazy loading purposes
        if (!initMount){
            document.querySelectorAll(".drp-dwn-child").forEach(item => item.addEventListener("click",(e) => this.changeState(e.target)) );
            document.querySelectorAll(".main-nav-link.Audio").forEach(item => item.children[0].addEventListener("click",(e) => this.changeState(e.target)) );

            initMount = true;
        }  
    }

    render() {
        const style = {
            width: "100%",
            height: "100%",
            position: "fixed"
        }

        window.addEventListener("pointermove", () => {
            if(!document.querySelector(".jq")) { /*console.log("jq appended");*/ apndJq(); } 
            if(!document.querySelector(".jq-ui")) { /*console.log("jq ui appended");*/ apndJqUI(); }
            //console.log("trying to apend scripts on pointermove");
        })

      return (
          <main>
            <div className="Parent-WS-Wrap" style={style}>
                {this.state.Audio && <Suspense fallback={this.renderLoader()}> <AudioWorkSpace/> </Suspense>}

                {this.state.Vision && <Suspense fallback={this.renderLoader()}> <VisionWorkSpace/> </Suspense>}

                {this.state.AV && <Suspense fallback={this.renderLoader()}> <AVWorkSpace/> </Suspense>}
                <InfoWorkSpace/>
            </div>
          </main>
      );
    }
  }

  export {ParentWorkSpace}